import React from 'react'
import Landing from "../components/layout/Landing"
import Navbar from "../components/layout/Navbar"
import Footer from "../components/layout/Footer"
import Register from "../components/auth/Register"
import Dashboard from "../components/dashboard/Dashboard"
import CreateProfile from "../components/create-profile/CreateProfile"
import EditProfile from "../components/edit-profile/EditProfile"
import AddExperience from "../components/add-credentials/AddExperience"
import AddEducation from "../components/add-credentials/AddEducation"
import Profiles from "../components/profiles/Profiles"
import Profile from "../components/profile/Profile"
import NotFound from "../components/not-found/NotFound"
import Posts from "../components/posts/Posts"
import Post from "../components/post/Post"

import Login from "../components/auth/Login"
//import PublicRouter from "./PublicRouter"
import PrivateRouter from "./PrivateRouter"

import { BrowserRouter as Router ,Route,Switch} from "react-router-dom";
export default ()=> {
  return (
     <Router>
      <div className="App">
        <Navbar />
        <Route exact={true} path="/" component={Landing} />
         <div className="container">

         <Switch><PrivateRouter exact={true} path="/dashboard" component={Dashboard} /> </Switch>
          <Switch><PrivateRouter exact={true} path="/create-profile" component={CreateProfile} />  </Switch>
        <Switch><PrivateRouter exact={true} path="/edit-profile" component={EditProfile} /> </Switch>
               <Switch><PrivateRouter exact={true} path="/add-experience" component={AddExperience} /> </Switch>
               <Switch><PrivateRouter exact={true} path="/add-education" component={AddEducation} /> </Switch>
               <Switch><PrivateRouter exact={true} path="/feed" component={Posts} /> </Switch>
               <Switch><PrivateRouter exact={true} path="/post/:id" component={Post} /> </Switch>

           <Route exact={true} path="/register" component={Register} />
           <Route exact={true} path="/login" component={Login} />
            <Route exact={true} path="/profiles" component={Profiles} />
            <Route exact={true} path="/profile/:handle" component={Profile} />
              <Route path="/not-found"  exact component={NotFound} />

         </div>
        <Footer />
      </div>
      </Router>
  )
}
