import React, { Component } from 'react'
import propTypes from "prop-types"
import {withRouter} from "react-router-dom"
import {connect} from "react-redux"
import {registeruser} from "../../actions/authActions"
import TextFieldGroup from "../common/TextFieldGroup"
 class Register extends Component {
  constructor(props){
    super(props);
    this.state={
      name:"",
      email:"",
      password:"",
      password2:"",
      errors:{}
    }
 
  }
  componentDidMount(){
    if(this.props.auth.isAuthenticated){
      this.props.history.push("/dashboard");
    }
  }
 // as Component Will receive props 
  static getDerivedStateFromProps(props, state) {
      return {
        errors:props.errors
      }
  }

  onChange=(e)=>{
    this.setState({[e.target.name]:e.target.value});
  }
  onSubmit=(e)=>{
    e.preventDefault();
    const newUser={
      name:this.state.name,
      email:this.state.email,
      password:this.state.password,
      password2:this.state.password2
    }

// to use history.push from action
 this.props.registeruser(newUser,this.props.history);
  }
  render() {
    const {errors}=this.state;
    const {user}=this.props.auth;
    return (
      <div>
         <div class="register">
           {user?user.name:null}
    <div className="container">
      <div className="row">
        <div className="col-md-8 m-auto">
          <h1 className="display-4 text-center">Sign Up</h1>
          <p className="lead text-center">Create your devNetwork account</p>
          <form onSubmit={this.onSubmit}>
           
                <TextFieldGroup
                placeholder="Name"
                name="name"
                value={this.state.name}
                onChange={this.onChange}
                error={errors.name}
                 />
              <TextFieldGroup
                placeholder="Email address"
                name="email"
                type="email"
                info="this site uses Grabates so if you want a profile image , use a Gravatar email"
                value={this.state.email}
                onChange={this.onChange}
                error={errors.email}
                 />
              <TextFieldGroup
                placeholder="password"
                name="password"
                type="password"
                value={this.state.password}
                onChange={this.onChange}
                error={errors.password}
                 />
            
              <TextFieldGroup
                placeholder="Confirm password"
                name="password2"
                type="password2"
                value={this.state.password2}
                onChange={this.onChange}
                error={errors.password2}
                 />
            <input type="submit" className="btn btn-info btn-block mt-4" />
          </form>
        </div>
      </div>
    </div>
  </div>

      </div>
    )
  }
}
// used to validatino and make sure of my data 
// e.g registeruser is function and required
// e.g auth is object and required
Register.propTypes={
  registeruser:propTypes.func.isRequired,
  auth:propTypes.object.isRequired,
  errors:propTypes.object.isRequired

}
const mapStateToProps=(state)=>({
  auth:state.auth,
  errors:state.errors
})

export default connect(mapStateToProps,{registeruser})(withRouter(Register));