
import {GET_PROFILES,PROFILE_LOADING,GET_PROFILE,CLEAR_CURRENT_PROFILE} from "../actions/types"
const initState={
  profile:null,
  profiles:null,  
  loading:false
}

export default (state=initState,action)=>{
  console.log(action.type)
  switch(action.type){
    case PROFILE_LOADING:
    return {
      ...state,
      
      loading:true
    }
    case GET_PROFILE :
    return {
      ...state ,
      profile:action.payload,
      loading:false
    }
    case GET_PROFILES :
    return {
      ...state,
      profiles:action.payload,
      loading:false
    }
    case CLEAR_CURRENT_PROFILE:return {
      ...state,
      profile:null,
    }
    default:
    return state
  }
}