import axios from "axios";
import { GET_PROFILE,GET_PROFILES, PROFILE_LOADING, GET_ERRORS ,CLEAR_CURRENT_PROFILE,SET_CURRENT_USER} from "./types";

// get current profile
export const getCurrentProfile = () => dispatch => {
  dispatch(setProfileLoading());
  axios
    .get("/api/profile")
    .then(res =>
      dispatch({
        type: GET_PROFILE,
        payload: res.data
      })
    )
    .catch(err => dispatch({ type: GET_PROFILE, payload: {} }));
};
// GET all profiles
export const getProfiles=()=>dispatch=>{
  dispatch(setProfileLoading());
  axios.get("/api/profile/all")
  .then(res=>dispatch({type:GET_PROFILES,payload:res.data}))
  .catch(err=>dispatch({type:GET_PROFILES,payload:null}))
}
// Delete account & profile 
export const deleteAccount=()=>dispatch=>{
  if(window.confirm("Are you sure ? you can NOT back your account again !")){
      axios.delete("/api/profile")
      .then(res=>dispatch({type:SET_CURRENT_USER,payload:{}}))
      .catch(err=>dispatch({type:GET_ERRORS,payload:err.response.data}))
  }
}
// get current profile
export const getProfileByHandle = (handle) => dispatch => {
  dispatch(setProfileLoading());
  axios
    .get(`/api/profile/handle/${handle}`)
    .then(res =>
      dispatch({
        type: GET_PROFILE,
        payload: res.data
      })
    )
    .catch(err => dispatch({ type: GET_PROFILE, payload: null }));
};
// profile loading

export const setProfileLoading = () => {
  return {
    type: PROFILE_LOADING
  };
};
// clear profile

export const clearCurrentProfile = () => {
  return {
    type: CLEAR_CURRENT_PROFILE
  };
};

// create profile
export const createProfile=(profileData,history)=>(
  dispatch=>{
    axios.post("/api/profile",profileData)
    .then(res=>history.push("/dashboard"))
    .catch(err=>
    dispatch({
      type:GET_ERRORS,
      payload:err.response.data
    })
    )
  }

)
export const addexperience=(expData,history)=>
  dispatch=>{
      axios.post("/api/profile/experience",expData)
      .then((res)=>history.push("/dashboard"))
      .catch(err=>dispatch({type:GET_ERRORS,payload:err.response.data}))
  }
//Delete Experience

export const deleteExperience=(id)=>
  dispatch=>{
      axios.delete(`/api/profile/experience/${id}`)
      .then(res=>
      dispatch({type:GET_PROFILE,payload:res.data})
      )
      .catch(err=>dispatch({type:GET_ERRORS,payload:err.response.data}))
  }

