import axios from "axios";
import {
  ADD_POST,
  GET_ERRORS,
  GET_POSTS,
  POST_LOADING,
  DELETE_POST,
  GET_POST
} from "./types.js"

// add post
export const addPost=postDate=>dispatch=>{
  axios.post("/api/posts",postDate)
  .then((res)=>(dispatch({type:ADD_POST,payload:res.data})))
  .catch(err=>
  dispatch({
    type:GET_ERRORS,
    payload:err.response.data
  })
  )
}

// get posts
export const getPosts=()=>dispatch=>{
  dispatch(setPostLoading);
  axios.get("/api/posts")
  .then((res)=>{console.log(res);dispatch({type:GET_POSTS,payload:res.data})})
  .catch(err=>
  dispatch({
    type:GET_POSTS,
    payload:null
  })
  )
}

// get posts
export const getPost=(id)=>dispatch=>{
  dispatch(setPostLoading);
  axios.get(`/api/posts/${id}`)
  .then((res)=>{dispatch({type:GET_POST,payload:res.data})})
  .catch(err=>
  dispatch({
    type:GET_ERRORS,
    payload:null
  })
  )
}
// add like
export const addLike=postId=>dispatch=>{
  console.log()
  axios.post(`/api/posts/like/${postId}`)
  .then((res)=>(dispatch(getPosts())))
  .catch(err=>
  dispatch({type:GET_ERRORS,payload:err.response.data})
  )
}
// remove like
export const removeLike=postId=>dispatch=>{
 
  axios.post(`/api/posts/unlike/${postId}`)
  .then((res)=>(dispatch(getPosts())))
  .catch(err=>
  dispatch({type:GET_ERRORS,payload:err.response.data})
  )
}
// delete post
export const deletePost=postId=>dispatch=>{

  axios.delete(`/api/posts/${postId}`)
  .then((res)=>(dispatch({type:DELETE_POST,payload:postId})))
  .catch(err=>
  dispatch({
    type:GET_POSTS,
    payload:null
  })
  )
}
// add comment
export const addComment=(postId,commentData)=>dispatch=>{
  axios.post(`/api/posts/comment/${postId}`,commentData)
  .then((res)=>(dispatch({type:GET_POST,payload:res.data})))
  .catch(err=>
  dispatch({
    type:GET_ERRORS,
    payload:err.response.data
  })
  )
}

// add comment
export const deleteComment=(postId,commentId)=>dispatch=>{
  axios.delete(`/api/posts/comment/${postId}/${commentId}`)
  .then((res)=>(dispatch({type:GET_POST,payload:res.data})))
  .catch(err=>
  dispatch({
    type:GET_ERRORS,
    payload:err.response.data
  })
  )
}
//set loading state
export const setPostLoading=()=>{
  return {
    type:POST_LOADING
  }
}