import {ADD_POST,GET_POSTS,GET_POST,DELETE_POST,POST_LOADING} from "../actions/types"
const initState={
  posts:[],
  post:{},
  loading:false
}
export default (state=initState,action)=>{
   switch (action.type){
     case POST_LOADING:
     return {
       ...state,
       loading:true
     }
     case DELETE_POST:return{
       ...state,
       posts:state.posts.filter(post=>post._id!==action.payload)
     }
     case GET_POSTS:
     return {
       ...state,
       posts:action.payload,
       loading:false
     }
     case ADD_POST:
     return {
       ...state,
       posts:[action.payload,...state.posts]
     }
     case GET_POST:
     return {
       ...state,
       post:action.payload,
       loading:false
     }
     default :return state
   }
}