import React, { Component } from 'react'
import {connect} from "react-redux"
import {withRouter} from "react-router-dom"
import PropTypes from "prop-types"
import TextFieldGroup from "../common/TextFieldGroup";
import TextAreaFieldGroup from "../common/TextAreaFieldGroup"
import InputGroup from "../common/InputGroup"
import SelectListGroup from "../common/SelectListGroup"
import {createProfile,getCurrentProfile} from "../../actions/profileActions"
import isEmpty from "../../validation/is-empty";


 class EditProfile extends Component {
   constructor(props){
     super(props)
     this.state={
       displaySocialInputs:false,
       handle:"",
       company:"",
       website:"",
       status:"",
       location:"",
       skills:"",
       githubusername:"",
       bio:"",
       twitter:"",
       facebook:"",
       linkedin:"",
       youtube:"",
       instagram:"",
       errors:{}
     }
   }
   
componentDidMount=()=>{
  this.props.getCurrentProfile();
}

static getDerivedStateFromProps=(props, state)=>{
        console.log("PROPS",props)
      if(!isEmpty(props.errors)){
        return {
          errors:props.errors
        }
      }
     console.log("STATE",state)

      if(props.profile.profile&&state.handle==""){
          const profile=props.profile.profile;

          //  bring skills array batck to CSV
          const skillsCSV=profile.skills.join(",");

          //  If profile field doesn't exist, make empty string 
          profile.company=!isEmpty(profile.company)?profile.company:"";
          profile.website=!isEmpty(profile.website)?profile.website:"";
          profile.location=!isEmpty(profile.location)?profile.location:"";
          profile.githubusername=!isEmpty(profile.githubusername)?profile.githubusername:"";
          profile.bio=!isEmpty(profile.bio)?profile.bio:"";
          profile.social.twitter=!isEmpty(profile.social.twitter)?profile.social.twitter:"";
          profile.social.facebook=!isEmpty(profile.social.facebook)?profile.social.facebook:"";
          profile.social.youtube=!isEmpty(profile.social.youtube)?profile.social.youtube:"";
          profile.social.instagram=!isEmpty(profile.social.instagram)?profile.social.instagram:"";
          profile.social.linkedin=!isEmpty(profile.social.linkedin)?profile.social.linkedin:"";
          //set component fields state
          return {
        handle:profile.handle,
       company:profile.company,
       website:profile.website,
       status:profile.status,
       location:profile.location,
       skills:skillsCSV,
       githubusername:profile.githubusername,
       bio:profile.bio,
       twitter:profile.social.twitter,
       facebook:profile.social.facebook,
       linkedin:profile.social.linkedin,
       youtube:profile.social.youtube,
       instagram:profile.social.instagram,
          }
      }
} 
   onChange=(e)=>{
     console.log(e.target.name)
      this.setState({[e.target.name]:e.target.value})
   }
   onSubmit=(e)=>{
      e.preventDefault();
      const profileData={
       handle:this.state.handle,
       company:this.state.company,
       website:this.state.website,
       location:this.state.location,
       skills:this.state.skills,
       status:this.state.status,
       githubusername:this.state.githubusername,
       bio:this.state.bio,
       twitter:this.state.twitter,
       facebook:this.state.facebook,
       linkedin:this.state.linkedin,
       youtube:this.state.youtube,
       instagram:this.state.instagram
      }
      this.props.createProfile(profileData,this.props.history)

  
   }
  render() {
    const {errors,displaySocialInputs}=this.state
    let socialInputs;
    if(displaySocialInputs){
      socialInputs=(
        <div>
          <InputGroup 
          placeholder="facebook profile url"
          name="facebook"
          icon="fab fa-facebook"
          value={this.state.facebook}
          onChange={this.onChange}
          error={errors.facebook}
          
          />
             <InputGroup 
          placeholder="instagram profile url"
          name="instagram"
          icon="fab fa-instagram"
          value={this.state.instagram}
          onChange={this.onChange}
          error={errors.instagram}
          
          />
             <InputGroup 
          placeholder="linkedin profile url"
          name="linkedin"
          icon="fab fa-linkedin"
          value={this.state.linkedin}
          onChange={this.onChange}
          error={errors.linkedin}
          
          />
             <InputGroup 
          placeholder="youtube channel url"
          name="youtube"
          icon="fab fa-youtube"
          value={this.state.youtube}
          onChange={this.onChange}
          error={errors.youtube}
          
          />
            <InputGroup 
          placeholder="twitter profile url"
          name="twitter"
          icon="fab fa-twitter"
          value={this.state.twitter}
          onChange={this.onChange}
          error={errors.twitter}
          
          />
        </div>
      )
    }else{

    }
    // select options for status
    const options=[{
    label:"* select profesional status",value:0}
    ,{label:"Developer",value:"Developer"}
    ,{label:"junior Developer",value:"junior Developer"}
    ,{label:"senior Developer",value:"senior Developer"}
    ,{label:"manager",value:"manager"}
    ,{label:"Student or learner",value:"Student"}
    ,{label:"intern",value:"intern"}

    ];
    
    return (
      <div className="create-profile">
          <div className="container">
            <div className="row">
              <div className="col-md-m-auto">
                <h1 className="display-4 text-center"> 
                  Edit profile
                </h1>
                <p className="lead text-center">
                let's get some info to make your profile start
                </p>
                <small className="d-block pb-3">
                * : required Fields
                </small>
                <form onSubmit={this.onSubmit}>
                  <TextFieldGroup 
                  placeholder="* profile handle"
                  name="handle"
                  value={this.state.handle}
                  onChange={this.onChange}
                  error={errors.handle}
                  info="choose unique handle for your profile URL "
                  />
                  <SelectListGroup 
                  name="status"
                  value={this.state.status}
                  onChange={this.onChange}
                  error={errors.status}
                  info="more info about your career"
                  options={options}
                  />
                  <TextFieldGroup 
                  placeholder="Company"
                  name="company"
                  value={this.state.company}
                  onChange={this.onChange}
                  error={errors.company}
                  info="should write your company or you work at"
                  />
                  <TextFieldGroup 
                  placeholder="website"
                  name="website"
                  value={this.state.website}
                  onChange={this.onChange}
                  error={errors.website}
                  info="write your website"
                  />
                   <TextFieldGroup 
                  placeholder="location"
                  name="location"
                  value={this.state.location}
                  onChange={this.onChange}
                  error={errors.location}
                  info="company location"
                  />
                   <TextFieldGroup 
                  placeholder="your Skills"
                  name="skills"
                  value={this.state.skills}
                  onChange={this.onChange}
                  error={errors.skills}
                  info="let people know your skills (e.g javascript , nodejs , php , etc... )"
                  />
                   <TextFieldGroup 
                  placeholder="Github username"
                  name="githubusername"
                  value={this.state.githubusername}
                  onChange={this.onChange}
                  error={errors.githubusername}
                  info="if you want to show your latest projects"
                  />
                   <TextAreaFieldGroup 
                  placeholder="Bio"
                  name="bio"
                  value={this.state.bio}
                  onChange={this.onChange}
                  error={errors.bio}
                  info="tell us about yourself"
                  />

                  <div className="mb-3">
                    <button   
                    type="button"
                     onClick={()=>{
                   
                      this.setState(prevState=>({
                        displaySocialInputs:!prevState.displaySocialInputs
                      }))
                    }} className="btn btn-light">
                    Add social networks links
                    </button>
                    <span className="text-muted">Optional</span>
                  </div>
                  {socialInputs}
                  <input type="submit" value="Submit" className="btn btn-info btn-block mt-4" />
                
                  
                </form>
              </div>
            </div>
          </div>
      </div>
    )
  }
}
EditProfile.propTypes={
  profile:PropTypes.object.isRequired,
  errors:PropTypes.object.isRequired
}
const mapStateToProps=(state)=>({
  profile:state.profile,
  errors:state.errors
})
export default connect(mapStateToProps,{createProfile,getCurrentProfile})(withRouter(EditProfile));